<template>
	<div class="add_qust_ansr">
		<el-form label-position="right" :rules="rules" ref="form" label-width="120px" :model="form">
			<el-row>
				<el-col :span="7">
				  	<el-form-item label="问答标题" prop="QStitle">
				    	<el-input size="medium" v-model="form.QStitle" placeholder="请输入问答标题"></el-input>
				  	</el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="发布位置" prop="location">
						<el-select size="medium" v-model="form.location" placeholder="选择发布位置">
							<el-option label="默认分类" value="2"></el-option>
							<el-option label="分类一" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="10">
					<el-form-item prop="QSdesc" label="问答描述">
						<el-input type="textarea" :rows="5" resize="none" v-model="form.QSdesc" placeholder="请输入问答描述"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<!--添加题目-->
			<el-form label-position="right" :rules="rules_1" ref="form_1" label-width="120px" :model="form_1">
				<el-row>
					<el-col :span="22">
						<el-card class="box-card">
							<div slot="header" class="clearfix">
							    <el-button type="default" icon="el-icon-circle-plus-outline" @click="addQuestion">添加问题</el-button>
							    <el-button type="default" icon="el-icon-view" @click="preview">预览</el-button>
							</div>
							<!--题目添加-->
						  	<div v-if="!show_flag" class="addNew_question">
						  		<el-row>
									<el-col :span="12">
									  	<el-form-item label="题目" prop="addQStitle">
									    	<el-input size="medium" v-model="form_1.addQStitle" placeholder="请输入题目"></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item label="备注" prop="addQSdesc">
									    	<el-input size="medium" v-model="form_1.addQSdesc" placeholder="请输入备注"></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
										<el-form-item label="题目类型" prop="addQStype">
											<el-select size="medium" v-model="form_1.addQStype" placeholder="选择题目类型">
												<el-option label="单选" value="2"></el-option>
												<el-option label="多选" value="1"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item label="题目排序">
									    	<el-input size="medium" v-model="form_1.addQSsort" placeholder="请输入大于0的整数"></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row v-for="(item,index) in form_1.selects" :key="index" >
									<el-col :span="12">
									  	<el-form-item :prop="'selects.' + index + '.value'" 
									  		:rules="{required: true, message: '题目选项不能为空', trigger: 'blur'}">
									    	<el-input size="medium" v-model="form_1.selects[index].value" placeholder="请输入题目选项" style="width: calc(100% - 60px);"></el-input>
									    	<el-button size="medium" @click.prevent="removeSelect(index)" icon="el-icon-delete"></el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item>
									    	 <el-button size="medium" icon="el-icon-circle-plus" @click="addSelect">新建选项</el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item>
									    	 <el-button size="medium" type='warning' @click="addSure('form_1')">确定</el-button>
									    	 <el-button size="medium" @click="addCancle">取消</el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
						  	</div>
						  	<!--题目展示-->
						  	<div v-else class="show_question">
						  		<div v-for="(item,index) in 3" class="question_item">
						  			<p>{{index+1}}、人口增长比率</p>
						  			<div v-if="index%2==0" class="answers_select">
						  				<p><input type="radio" name="radio" />5%</p>
						  				<p><input type="radio" name="radio" />15%</p>
						  				<p><input type="radio" name="radio" />25%</p>
						  			</div>
						  			<div v-else="" class="answers_select">
						  				<p><input type="checkbox" name="" />5%</p>
						  				<p><input type="checkbox" name="" />15%</p>
						  				<p><input type="checkbox" name="" />25%</p>
						  			</div>
						  		</div>
						  	</div>
						</el-card>	
					</el-col>
				</el-row>
			</el-form>
			<br />
			<br />
			<!--添加题目结束-->
			<el-row>
				<el-col>
					<el-form-item>
						<el-button type="primary" @click="submit('form')">确认</el-button>
						<el-button @click="back">取消</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_qust_ansr",
		data(){
			return{
				form:{
					QStitle : "",			//问答标题
					location: "",			//发布位置
					QSdesc  : "",			//问答描述
				},
				form_1:{
					addQStitle: "",			//添加问搭的标题
					addQSdesc : "",			//添加问搭的备注
					addQStype : "",			//添加问搭的类型
					addQSsort : "",			//添加问搭的排序
					selects: [				//问题选项
						{ value: '' },
					],
				},
				rules: {
					QStitle   : [{ required: true, message: '请输入问答标题', trigger: 'blur' }],
					location  : [{ required: true, message: '请选择发布位置', trigger: 'blur' }],
					QSdesc    : [{ required: true, message: '请输入问答描述', trigger: 'blur' }],
				},
				rules_1: {
					addQStitle: [{ required: true, message: '请输入题目', trigger: 'blur' }],
					addQStype : [{ required: true, message: '请选择题目类型', trigger: 'blur' }],
				},
				show_flag: true,			//题目和添加开关
			}
		},
		methods:{
			//添加
			addQuestion(){
				this.show_flag = false;
			},
			//预览
			preview(){
				this.$router.push({path: "qust_ansr_preview"})
			},
			//新增选项
			addSelect(){
				this.form_1.selects.push({value: ''});
			},
			//删除选项
			removeSelect(index){
	          	this.form_1.selects.splice(index, 1)
			},
			//新增确定
			addSure(form_1){
				console.log(this.form)
				this.$refs[form_1].validate((valid) => {
		          	if (valid) {
		           	 	console.log("提交成功")
		           	 	this.show_flag = true;
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			//新增取消
			addCancle(){
				this.show_flag = true;
			},
			submit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("提交成功")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			back(){
				this.$router.go(-1)
			},
		}
	})
</script>

<style scoped="scoped" lang="less">
	.add_qust_ansr .el-card.box-card{margin-left: 45px;}
	.add_qust_ansr .box-card .el-select{width: 100%;}
	
	.add_qust_ansr{
		.box-card {
			.show_question{
				height: 470px;
				overflow-y: auto;
				margin-left: 50px;
				.question_item{
					padding: 15px 20px;
					margin-bottom: 25px;
					width: calc(~"100% - 100px");
					background: #f5f5f5;
					border-radius: 5px;
					.answers_select{
						p{
							margin-top: 15px ;
							input{margin-right: 10px; margin-left: 25px;}
						}
					}
				}
			}
		}
	} 
</style>


