<template>
	<div class="add_user">
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
			<el-row>
				<el-col :span="9">
					<el-form-item label="姓名" prop="name">
						<el-input size="medium" v-model="ruleForm.name" placeholder="请输入真实姓名"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="昵称" prop="nickName">
						<el-input size="medium" v-model="ruleForm.nickName" placeholder="请输入昵称"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="用户手机号" prop="phone">
						<el-input size="medium" v-model="ruleForm.phone" placeholder="请输入手机号"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="性别" prop="sex">
						<el-select size="medium" v-model="ruleForm.sex" placeholder="请选择性别">
							<el-option label="男" value="1"></el-option>
							<el-option label="女" value="0"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="所属党支部" prop="partyBranch">
						<el-select size="medium" v-model="ruleForm.partyBranch" placeholder="请选择所属党支部">
							<el-option label="中共党支部" value="1"></el-option>
							<el-option label="河南党支部" value="0"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="身份证号" prop="personal_ID">
						<el-input size="medium" v-model="ruleForm.personal_ID" placeholder="请输入身份证号"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9">
					<el-form-item label="上传用户头像" prop="user_icon">
						<el-upload class="avatar-uploader" action="https://tony" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
							<img v-if="ruleForm.imageUrl" :src="ruleForm.imageUrl" class="avatar">
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="9" :offset="3">
					 <el-button type="primary" size="medium" @click="submit('ruleForm')">提交</el-button>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_user",
		data() {
			var checkPhone = (rule, value, callback) => {
		        if (value === '') {
		          	callback(new Error('请输入手机号'));
		        } else if (!/^1[3|4|5|7|8][0-9]{9}$/.test(value)) {
		          	callback(new Error('手机号码格式错误'));
		        } else {
		          	callback();
		        }
		    };
		    var checkPersonID = (rule, value, callback) => {
		        if (value === '') {
		          	callback(new Error('请输入身份证号'));
		        } else if (!/^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[A-Z])$/.test(value)) {
		          	callback(new Error('身份证号码格式错误'));
		        } else {
		          	callback();
		        }
		    };
			return {
				ruleForm: {
					name: "",
					nickName: "",
					phone: "",
					sex: "",
					partyBranch: "",
					personal_ID: "",
					imageUrl: '',
				},
				rules: {
					name       : [{ required: true, message: '请输入真实姓名', trigger: 'blur' }],
					nickName   : [{ required: true, message: '请输入昵称', trigger: 'blur' }],
					phone      : [{ required: true, validator: checkPhone, trigger: 'blur' }],
					sex        : [{ required: true, message: '请选择性别', trigger: 'blur' }],
					partyBranch: [{ required: true, message: '请选择党支部', trigger: 'blur' }],
					personal_ID: [{ required: true, validator: checkPersonID, trigger: 'blur' }],
					user_icon  : [{ required: true, message: '请选择用户头像', trigger: 'blur' }]
				},
			}
		},
		methods: {
			handleAvatarSuccess(res, file) {
				this.ruleForm.imageUrl = URL.createObjectURL(file.raw);
			},
			beforeAvatarUpload(file) {
//				const isJPG = file.type === 'image/jpeg';
				const isLt2M = file.size / 1024 / 1024 < 2;

//				if(!isJPG) {
//					this.$message.error('上传头像图片只能是 JPG 格式!');
//				}
				if(!isLt2M) {
					this.$message.error('上传头像图片大小不能超过 2MB!');
				}
//				return isJPG && isLt2M;
				return isLt2M;
			},
			//提交
			submit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("提交成功")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			}
		}
	})
</script>

<style>
	/*select选择狂*/
	.add_user .el-select{width: 100%;}
	
	/*图片上传*/
	.add_user .avatar-uploader .el-upload {
		border: 1px dashed #8c939d;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	
	.add_user .avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}
	
	.add_user .avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}
	
	.add_user .avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
</style>


