<template>
	<div class="add_words">
		<el-form label-position="left" label-width="120px" :model="formInline" :rules="rules" ref="formInline">
			<el-form-item>
				<div>每个词语请用 ‘,’隔开，不区分半角全角写</div>
			</el-form-item>
			<el-form-item prop="textarea">
				<el-col :span="8">
					<el-input type="textarea" :rows="15" resize="none" placeholder="请输入敏感词汇" v-model="formInline.textarea"></el-input>
				</el-col>
			</el-form-item>
			<el-row>
				<el-col :span="8">
					<el-form-item>
					    <el-button type="primary" size="medium" @click="submitForm('formInline')">新建完成</el-button>
					    <el-button size="medium" @click="go_back">返回</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name : "add_words" ,
		data(){
			return{
				formInline:{
					textarea : ""
				},
				rules:{
					textarea: [
			            { required: true, message: '请填写敏感词汇', trigger: 'blur' },
			        ]
				}
			}
		},
		methods:{
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		            alert('submit!');
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		    },
		    go_back() {
		    	this.$router.push({path:"sensitive_words"})
		    }
		}
	})
</script>

<style scoped="scoped" lang="less">
	
</style>


