<template>
	<div class="carousel_list">
		<el-button size="medium" type="primary" @click="addNew">新建轮播图</el-button>
		<el-button size="medium" type="warning" @click="batchDel">批量删除</el-button>
		<br />
		<br />
		<br />
		<!--列表-->
		<el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
			<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" 
				:prop="item.prop" :label="item.name" :width="item.width" 
				:show-overflow-tooltip="item.tooltip">
			</el-table-column>
			<el-table-column label="图片" width="100" align="center"> 
				<template slot-scope="scope"> 
					<img :src="scope.row.img" width="60" height="60"/>
				</template> 
			</el-table-column>
			<el-table-column label="操作" width="180" align="center">
				<template slot-scope="scope">
					<el-button size="mini" type="text" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="text" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
					<el-button size="mini" type="text" @click="useSwitch(scope.$index, scope.row)">启用/禁用</el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default({
		name: "carousel_list",
		data() {
			return{
				tableHeade: [
					{name: "轮播图ID",   	prop: "id",      		width: 100,	tooltip: false},
					{name: "轮播图标题",	prop: "carousel_title", width: "", 	tooltip: true},
					{name: "位置展示",	prop: "show_location",	width: 140, tooltip: false},
					{name: "排序",   	prop: "sort",			width: 80, 	tooltip: false},
					{name: "状态",		prop: "status",			width: 100, tooltip: false},
					{name: "更新时间",	prop: "update_time",	width: 140, tooltip: false},
//					{name: "图片",		prop: "img",			width: 180, tooltip: false, scope: true},
				],
				tableData: [
					{
						id: "01", 
						carousel_title: '全球经济；你总是写了又看，让我沉默在惊奇的失望里', 
						show_location: "学习天地", 
						sort:"1",
						status:"启用",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
					{
						id: "01", 
						carousel_title: '全球经济', 
						show_location: "学习天地", 
						sort:"1",
						status:"启用",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
					{
						id: "01", 
						carousel_title: '全球经济', 
						show_location: "学习天地", 
						sort:"1",
						status:"启用",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
				]
			}
		},
		methods: {
			//添加
			addNew() {
				this.$router.push({path: "add_carousel"})
			},
			//批量删除
			batchDel() {
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				})
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
      		//编辑
			handleEdit(index, row) {
				console.log(index, row);
				this.$router.push({path: "add_carousel", query:{reName: "编辑轮播图"}})
			},
			//删除
			handleDelete(index, row) {
				var that = this;
				console.log(index, row);
				that.$confirm('确定删除？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {});
			},
			//启用禁用
			useSwitch(index, row){
				var that = this;
				console.log(index, row);
			}
		}
	})
</script>

<style>

</style>


