<template>
	<div class="add_carousel">
		<el-form label-position="right" :rules="rules" ref="form" label-width="120px" :model="form">
			<el-row>
				<el-col :span="7">
				  	<el-form-item label="轮播图名称" prop="carousel_name">
				    	<el-input size="medium" v-model="form.carousel_name" placeholder="请输入轮播图名称"></el-input>
				  	</el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="轮播图位置" prop="carousel_location">
						<el-select size="medium" v-model="form.carousel_location" placeholder="选择轮播图位置">
							<el-option label="默认分类" value="2"></el-option>
							<el-option label="分类一" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col>
					<el-form-item label="广告图片" prop="dialogImageUrl">
						<el-upload
						  action="https://tony"
						  list-type="picture-card"
						  :on-preview="handlePictureCardPreview"
						  :on-remove="handleRemove"
						  :limit="1"
  						  :on-exceed="handleExceed">
						  <i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible" size="tiny">
						  <img width="100%" :src="form.dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="轮播图链接地址" prop="carousel_url">
						<el-input size="medium" v-model="form.carousel_url" placeholder="请填写轮播图链接地址"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="轮播图顺序">
						<el-input size="medium" v-model="form.carousel_order" placeholder="输入1-5的顺序符"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="是否开启">
						<el-switch v-model="form.carousel_switch" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item>
						<el-button type="primary" size="small" @click="submit('form')">新建完成</el-button>
						<el-button size="small" @click="back">返回</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_carousel",
		data(){
			return{
				form: {
					carousel_name     : '',			//轮播图名称
					carousel_location : "",			//轮播图位置
					dialogImageUrl    : '',			//上传图片
					carousel_url      : "",			//轮播图连接
					carousel_order    : "",			//轮播图顺序
					carousel_switch   : true,		//是否开启
				},
				rules: {
					carousel_name     : [{ required: true, message: '请输入轮播图名称', trigger: 'blur' }],
					carousel_location : [{ required: true, message: '请选择轮播图位置', trigger: 'blur' }],
					dialogImageUrl    : [{ required: true, message: '请上传轮播图', trigger: 'blur' }],
					carousel_url      : [{ required: true, message: '请输入轮播图链接地址', trigger: 'blur' }],
				},
        		dialogVisible: false
			}
		},
		methods:{
			submit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("提交成功")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			back(){
				this.$router.go(-1)
			},
			handleRemove(file, fileList) {
		        console.log(file, fileList);
		    },
		    handlePictureCardPreview(file) {
		        this.form.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		    },
		    //图片限制提示
		    handleExceed(files, fileList) {
		        this.$message.warning(`当前限制选择 5个文件，共选择了 ${files.length + fileList.length} 个文件`);
		    }
		}
	})
</script>

<style>
</style>


