<template>
	<div class="sign_in">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<!--选择开始、结束时间-->
				<el-col :span="4">
					<el-form-item prop="date1">
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line">至</el-col>
				<el-col :span="4">
					<el-form-item prop="date2">
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--按钮-->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search">搜索</el-button>
					</el-form-item>
				</el-col>
				<!--新建帖子-->
				<el-col :span="3" :offset="6">
					<el-form-item>
						<el-button type="primary" size="medium" @click="addNew">新建签到</el-button>
					</el-form-item>
				</el-col>
				<!--批量删除-->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!--签到列表-->
		<div class="signIn_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="操作" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
						<el-button type="text" size="small" @click="check(scope.$index)">查看</el-button>
						<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "sign_in",
		data(){
			return{
				formInline:{
					date1: "",
					date2: "",
				},
				tableHeade: [
					{name: "签到人",  	prop: "name",     	width: 120,	tooltip: false},
					{name: "签到时间", 	prop: "time",   	width: 160,	tooltip: false},
					{name: "所属党支部", 	prop: "partyBranch",width: "", 	tooltip: true},
					{name: "签到总次数", 	prop: "totalNum", 	width: 120, tooltip: false},
				],
				tableData: [
					{ name: 77, time: '2016-05-03', partyBranch: '中华人民共和国中央人民政府浙江省日本市大阪县党支部', totalNum: 1123 },
					{ name: 77, time: '2016-05-03', partyBranch: '中华人民共和国中央人民政府浙江省日本市大阪县党支部', totalNum: 1123 },
					{ name: 77, time: '2016-05-03', partyBranch: '中华人民共和国中央人民政府浙江省日本市大阪县党支部', totalNum: 1123 },
				],
			}
		},
		methods:{
			//搜索
			search() {
				console.log('search!');
			},
			//新建
			addNew(){
				this.$router.push({path: "/sign_in_add"})
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				}).catch(()=>{})
			},
			//编辑
			edit(index,scope){
				console.log("编辑",index, scope)
				this.$router.push({path: "/sign_in_add", query:{reName: "编辑签到"}})
			},
			//查看
			check(index){
				console.log("查看",index)
				this.$router.push({path: "sign_in_check"})
			},
			//删除
			delet(index){
				console.log("删除",index)
				var that = this;
				console.log("删除",index)
				that.$confirm('确定删除？', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.sign_in{
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


