<template>
	<div class="qust_ansr">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<!--根据分类查询-->
				<el-col :span="3">
					<el-form-item>
						<el-select size="medium" v-model="formInline.classify" placeholder="根据分类查询">
							<el-option label="默认分类" value="2"></el-option>
							<el-option label="分类一" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<!--属性分类查询-->
				<el-col :span="3">
					<el-form-item>
						<el-select size="medium" v-model="formInline.attr_classify" placeholder="属性分类查询">
							<el-option label="精华" value="0"></el-option>
							<el-option label="制定" value="2"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<!--参与数查询-->
				<el-col :span="3">
					<el-form-item>
						<el-select size="medium" v-model="formInline.participate" placeholder="参与数查询">
							<el-option label="多到少" value="0"></el-option>
							<el-option label="少到多" value="2"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<!--选择开始、结束时间-->
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line">至</el-col>
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--帖子名称-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.posts_name" placeholder="请输入帖子名称"></el-input>
					</el-form-item>
				</el-col>
				<!--搜索-->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search">搜索</el-button>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<!--新建帖子-->
				<el-col :span="2">
					<el-form-item>
						<el-button type="primary" size="small" @click="addNew">新建问答</el-button>
					</el-form-item>
				</el-col>
				<!--批量删除-->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="small" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--帖子列表-->
		<div class="qust_ansr_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="操作" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
						<el-button type="text" size="small" @click="audit(scope.$index)">审核</el-button>
						<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "qust_ansr",
		data(){
			return{
				formInline: {
					classify     : '',			//分类
					attr_classify: "",			//属性分类
					participate  : "",			//参与查询
					date1        : "",			//开始时间
					date2        : "",			//结束时间
					posts_name   : "",			//帖子名称
				},
//				rules: {
//					date1: [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
//					date2: [{ type: 'date', required: true, message: '请选择时间', trigger: 'change' }],
//				},
				tableHeade: [
					{name: "问答ID",  prop: "id",     width: 120, tooltip: false},
					{name: "问答名称", prop: "name",   width: "",  tooltip: true},
					{name: "问答时间", prop: "date",   width: 160, tooltip: false},
					{name: "发布位置", prop: "locate", width: 120, tooltip: false},
					{name: "审核",    prop: "audit",  width: 120, tooltip: false},
					{name: "参与数",  prop: "joinNum", width: 120, tooltip: false},
					{name: "浏览量",  prop: "browse",  width: 120, tooltip: false},
				],
				tableData: [
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
					{ id: 77, date: '2016-05-03', name: '王小虎', locate: '学习', audit:"失败", joinNum: 1123, browse: 99999 },
				],
			}
		},
		methods:{
			//搜索
			search() {
				console.log('search!');
			},
			//新建
			addNew(){
				this.$router.push({path: "/add_qust_ansr"})
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				}).catch(()=>{})
			},
			//编辑
			edit(index,scope){
				console.log("编辑",index, scope)
				this.$router.push({path: "/add_qust_ansr", query:{reName: "编辑问答"}})
			},
			//审核
			audit(index){
				console.log("审核",index)
				var that = this;
				console.log("审核",index)
				that.$confirm('审核是否通过？', '提示', {
		          	confirmButtonText: '通过',
		          	cancelButtonText: '拒绝',
		          	closeOnClickModal: false,
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '审核成功!'
		          	});
		        }).catch(() => {
		        	that.$prompt('请输入拒绝理由', '提示',{
		        		inputPattern:  /\S/,
		        		inputErrorMessage: "请输入拒绝理由",
		        		closeOnClickModal: false
		        	}).then(({ value }) => {
			          	that.$message({
			            	type: 'success',
			            	message: '拒绝原因提交成功'
			          	});
			        }).catch(()=>{});         
		        });
			},
			//删除
			delet(index){
				console.log("删除",index)
				var that = this;
				console.log("删除",index)
				that.$confirm('确定删除？', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.qust_ansr{
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


