<template>
	<div class="add_classify">
		<el-form ref="form" :rules="rules" :model="form" label-width="120px">
			<el-row>
				<el-col :span="7">
					<el-form-item prop="classify_name" label="分类名称：">
						<el-input v-model="form.classify_name" width="160px" placeholder="请输入分类名称"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="10">
					<el-form-item prop="classify_desc" label="分类描述：">
						<el-input type="textarea" :rows="5" resize="none" v-model="form.classify_desc" placeholder="请输入分类描述"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-form-item>
				<el-button size="medium" type="primary" @click="onSubmit('form')">保存</el-button>
				<el-button size="medium" @click="back">返回</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_classify",
		data() {
			return {
				form:{
					classify_name:"",
					classify_desc:""
				},
				rules: {
          			classify_name: [
            			{ required: true, message: '请输入分类名称', trigger: 'blur' },
          			],
          			classify_desc: [
            			{ required: true, message: '请输入分类描述', trigger: 'blur' },
          			]
          		}
			}
		},
		methods:{
			onSubmit(form){
				 this.$refs[form].validate((valid) => {
          			if (valid) {
          				console.log(this.form)
          			} else {
            			console.log('error submit!!');
            		return false;
          		}
        	});
			},
			back(){
				this.$router.go(-1)
			}
		}
	})
</script>

<style>

</style>


