<template>
	<div class="content_classify">
		<el-button type="primary" size="small" @click="addNewClassify">新建帖子分类</el-button><br /><br /><br />
		<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 90%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55"></el-table-column>
			<el-table-column align="center" prop="sort_name" label="分类名称" width="160"></el-table-column>
			<el-table-column align="center" prop="sort_describe" label="分类描述" show-overflow-tooltip></el-table-column>
			<el-table-column align="center" prop="post_num" label="帖子数" width="120"></el-table-column>
			<el-table-column align="center" fixed="right" label="操作" width="100">
		      	<template slot-scope="scope">
			        <el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
			        <el-button type="text" size="small" @click="delet(scope.row)">删除</el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default {
		name: "ContentClassify",
		data() {
			return {
				tableData: [
					{sort_name: "学习园地", sort_describe:"当前用来存储全部的帖子", post_num: 999},
					{sort_name: "学习园地", sort_describe:"当前用来存储全部的帖子", post_num: 999},
					{sort_name: "学习园地", sort_describe:"当前用来存储全部的帖子", post_num: 999},
				]
			}
		},
		methods: {
			//新建分类
			addNewClassify() {
				this.$router.push({path: "add_classify"})
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//编辑
			edit(){
				this.$router.push({path: "add_classify", query:{reName: "编辑帖子分类"}})
			},
			//删除
			delet(index){
				var that = this;
				console.log("删除",index)
				that.$confirm('确定删除？', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	}
</script>

<style>

</style>


