<template>
	<div class="participan">
		<el-form class="demo-form-inline" label-width="140px">
			<el-row>
				<el-col :span="12">
					<el-form-item label="内容标题：">
						<el-input v-model="contentTitle" readonly></el-input>
					</el-form-item>	
				</el-col>
			</el-row>
		</el-form>
		<br />
		<br />
		<!--参与列表-->
		<div class="user_list">
			<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 90%">
				<el-table-column align="center" prop="name" label="用户名" width="120"></el-table-column>
				<el-table-column align="center" prop="time" label="参与时间" show-overflow-tooltip></el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name:"participan",
		data(){
			return{
				contentTitle: "10月70城房价指数出炉 杭州微涨0.2% 金花涨幅最大",
				tableData: [
					{ name: 77, time: '2017-12-07'},
					{ name: 77, time: '2017-12-07'},
					{ name: 77, time: '2017-12-07'},
				],
			}
		},
		methods:{
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style>
</style>


