<template>
	<div class="admin_group">
		<el-form :inline="true"  class="demo-form-inline">
			<el-row>
				<el-col :span="2" :offset="18">
					<el-form-item>
						<el-button type="primary" size="medium" @click="addGroup">添加分组</el-button>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--管理员分组列表-->
		<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width:100%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
		    <el-table-column v-for="item in tableHeader" :key="item.prop" :prop="item.prop" :label="item.name" :show-overflow-tooltip="item.tooltip" :width="item.width" align="center"></el-table-column>
		    <el-table-column align="center" label="操作" width="260" fixed="right">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
					<el-button type="text" size="small" @click="disabled(scope.$index)">禁用</el-button>
					<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>
<script>
	export default({
		name : "admin_group" ,
		data(){
			return{
				tableHeader:[
		      		{ name:"管理员组名" , prop:"adminGroup_name"   , width:140 , tooltip:false},
		      		{ name:"添加时间"   , prop:"add_time"     , width:"" ,  tooltip:true},
		        ],
		        tableData: [
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
		      		{adminGroup_name: '王小虎',add_time: '2016-05-03'},
			    ],
			}
		},
		methods:{
			//编辑
			edit(index,scope){
//				console.log("编辑",index, scope)
				this.$router.push({path:"add_group", query:{reName: "编辑分组"}})
			},
			//禁用
			disabled(index){
				var that = this;
				that.$confirm('是否禁用？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '禁用成功!'
		          	});
		        }).catch(()=>{});
			},
			//删除
			delet(index){
				var that = this;
				that.$confirm('确定删除吗？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(()=>{});
			},
			//添加分组
			addGroup(){
				this.$router.push({path:"add_group"})
			},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				})
			},
			//全选
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
		        console.log(val)
		    },
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.demo-form-inline{
		text-align: right;
	}
</style>


