<template>
	<div class="admin_list">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<!--权限查询下拉框-->
				<el-col :span="3">
					<el-form-item label="">
					    <el-select v-model="formInline.region" placeholder="管局权限查询" size="medium">
					      	<el-option label="管局权限查询" value="0"></el-option>
					      	<el-option label="全部" value="1"></el-option>
					      	<el-option label="总管理" value="2"></el-option>
					      	<el-option label="党支部1" value="3"></el-option>
					      	<el-option label="党支部2" value="4"></el-option>
					    </el-select>
					</el-form-item>
				</el-col>
				<!--管理员姓名-->
				<el-col :span="3">
					<el-form-item label="">
				    	<el-input v-model="formInline.admin_name" placeholder="输入管理员姓名" size="medium"></el-input>
					</el-form-item>
				</el-col>
				<!--选择开始、结束时间-->
				<el-col :span="3">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line" :span="1">至</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--查询按钮-->
				<el-col :span="2" :offset="2">
					<el-form-item>
					    <el-button type="info" size="medium" @click="onSearch">搜索</el-button>
					</el-form-item>
				</el-col>
				<!--添加管理员-->
				<el-col :span="2">
					<el-form-item>
					    <el-button type="primary" size="medium" @click="addAdmin">添加管理员</el-button>
					</el-form-item>
				</el-col>
				<!--批量删除-->
				<el-col :span="2" :offset="1">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--管理员列表-->
		<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width:100%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
		    <el-table-column v-for="item in tableHeader" :key="item.prop" :prop="item.prop" :label="item.name" :show-overflow-tooltip="item.tooltip" :width="item.width" align="center"></el-table-column>
		    <el-table-column align="center" label="操作" width="260" fixed="right">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
					<el-button type="text" size="small" @click="disabled(scope.$index)">禁用</el-button>
					<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name : "admin_list" ,
		data(){
			return {
			    formInline: {
			        admin_name: '',
			        region: '',
			        date1: '',
		      		date2: '',
		        },
		        tableHeader:[
		      		{ name:"管理员姓名" , prop:"admin_name"   , width:140 , tooltip:false},
		      		{ name:"管理员账号" , prop:"admin_num"    , width:160 , tooltip:false},
		      		{ name:"所属权限"   , prop:"admin_power"  , width:140 , tooltip:false},
		      		{ name:"添加时间"   , prop:"add_time"     , width:"" ,  tooltip:true},
		      		{ name:"状态"      , prop:"admin_statue" , width:120 , tooltip:false},
		        ],
		        tableData: [
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
		      		{admin_name: '王小虎',admin_num:"admin",admin_power:"总管理",add_time: '2016-05-03',admin_statue:'正常'},
			    ],
			}
		},
		methods:{
			//搜索
			onSearch() {
		        console.log('搜索');
		    },
		    //跳转到添加管理员页面
		    addAdmin(){
		    	this.$router.push({path:"add_admin"})
		    },
		    //全选
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
		        console.log(val)
		    },
		    //批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				})
			},
		    //编辑
			edit(index,scope){
//				console.log("编辑",index, scope)
				this.$router.push({path:"add_admin", query:{reName: "编辑管理员"}})
			},
			//禁用
			disabled(index){
				var that = this;
				that.$confirm('是否禁用？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '禁用成功!'
		          	});
		        }).catch(()=>{});
			},
			//删除
			delet(index){
				var that = this;
				that.$confirm('确定删除吗？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(()=>{});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		},
	})
</script>

<style scoped="scoped" lang="less">
	.admin_list {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


