<template>
	<div class="add_group">
		<el-form :inline="true"  label-width="120px" class="demo-form-inline" :rules="rules" ref="formInline" :model="formInline" >
			<!--添加组名-->
			<el-row>
				<el-col :span="8">
					<el-form-item label="添加组名" prop="group_name">
				    	<el-input v-model="formInline.group_name" placeholder="建议输入党支部名称"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
		  	<!--内容权限-->
		  	<div class="content_permissions">
		  		<el-checkbox :indeterminate="isIndeterminate1" v-model="checkAll1" @change="handleCheckAllChange1" style="margin-left: 45px;">内容</el-checkbox>
			  	<div style="margin: 15px 0;"></div>
			  	<div class="content_list" v-for="(item,index) in data1" :label="index" :key="index">
			  		<span class="content_list_span">{{item.name}}</span>
			  		<el-checkbox-group v-model="checkedBox1" @change="handleCheckedCitiesChange1">
				    	<el-checkbox v-for="item_son in item.children" :label="item_son.id" :key="item_son.id">{{item_son.title}}</el-checkbox>
					</el-checkbox-group>
			  	</div>
		  	</div>
		  	<!--用户管理权限-->
		  	<div class="user_permissions">
		  		<el-checkbox :indeterminate="isIndeterminate2" v-model="checkAll2" @change="handleCheckAllChange2" style="margin-left: 45px;">用户管理</el-checkbox>
			  	<div style="margin: 15px 0;"></div>
			  	<div class="content_list" v-for="(item,index) in data2" :label="index" :key="index">
			  		<span class="content_list_span">{{item.name}}</span>
			  		<el-checkbox-group v-model="checkedBox2" @change="handleCheckedCitiesChange2">
				    	<el-checkbox v-for="item_son in item.children" :label="item_son.id" :key="item_son.id">{{item_son.title}}</el-checkbox>
					</el-checkbox-group>
			  	</div>
		  	</div>
		  	<el-row>
				<el-col :offset="1">
					<el-form-item>
					    <el-button type="primary" size="medium" @click="submitForm('formInline')">提交</el-button>
					    <el-button size="medium" @click="resetForm('formInline')">取消</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>
<script>
  const data1 = [
  			{
  				name:"内容列表",
  				Ident:"message_list",
  				children:[
	  				{title:"编辑",id:2},
	  				{title:"删除",id:3},
	  				{title:"审核",id:4},
	  				{title:"新建帖子",id:5},
  				]		
  			},
  			{
  				name:"分类列表",
  				Ident:"sort_list",
  				children:[
  					{title:"新建",id:6},
  					{title:"编辑",id:7},
  					{title:"删除",id:8},
  					{title:"审核",id:9},
  				]
  			},
  			{
  				name:"帖子互动",
  				Ident:"post_list",
  				children:[
  					{title:"编辑",id:10},
  					{title:"删除",id:11},
  				]
  			}
  	]
    const data2 = [
  			{
  				name:"用户列表",
  				Ident:"user_list",
  				children:[
	  				{title:"添加",id:12},
	  				{title:"编辑",id:13},
	  				{title:"删除",id:14},
	  				{title:"审核",id:15},
	  				{title:"拉黑",id:16},
  				]		
  			},
  			{
  				name:"党支部分组",
  				Ident:"branch_group",
  				children:[
  					{title:"编辑",id:17},
  					{title:"删除",id:18},
  					{title:"审核",id:19},
  					{title:"新建分组",id:20},
  				]
  			}
  	]
  export default {
    data() {
      return {
      	formInline:{
			group_name : "",
		},
		rules:{
			group_name: [
	            { required: true, message: '请输入组名', trigger: 'blur' },
	        ],
		},
        checkAll1: false,
        checkedBox1: [],
        isIndeterminate1: false,
        data1:data1,
        checkAll2: false,
        checkedBox2: [],
        isIndeterminate2: false,
        data2:data2
      };
    },
    methods: {
      handleCheckAllChange1(val) {
      	var data1_id = []
      	for(var i = 0 ; i < data1.length ; i++){
      		for(var j = 0 ; j < data1[i].children.length ; j++){
      			data1_id.push(data1[i].children[j].id)
      		}
      	}
        this.checkedBox1 = val ? data1_id : [];
        this.isIndeterminate1 = false;
      },
      handleCheckedCitiesChange1(value) {
        let checkedCount = value.length;
        var len = 0 //循环后各个数组里面的长度之和
        for(var i = 0 ; i < data1.length ; i++){
        	len = len + data1[i].children.length
        }
        this.checkAll1 = checkedCount === len;
        this.isIndeterminate1 = checkedCount > 0 && checkedCount < len;
      },
      handleCheckAllChange2(val) {
      	var data2_id = []
      	for(var i = 0 ; i < data2.length ; i++){
      		for(var j = 0 ; j < data2[i].children.length ; j++){
      			data2_id.push(data2[i].children[j].id)
      		}
      	}
        this.checkedBox2 = val ? data2_id : [];
        this.isIndeterminate2 = false;
      },
      handleCheckedCitiesChange2(value) {
        let checkedCount = value.length;
        var len = 0 //循环后各个数组里面的长度之和
        for(var i = 0 ; i < data2.length ; i++){
        	len = len + data2[i].children.length
        }
        this.checkAll2 = checkedCount === len;
        this.isIndeterminate2 = checkedCount > 0 && checkedCount < len;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
	        if (valid) {
	           alert('submit!');
	        } else {
	           console.log('error submit!!');
	           return false;
	        }
        });
        if(this.checkedBox1.length == 0 && this.checkedBox2.length == 0){
        	this.$message.error('请勾选权限');
        }
        console.log(this.checkedBox1,this.checkedBox2)
	 },
     resetForm(formName) {
        this.$refs[formName].resetFields();
        this.checkedBox1 = []
        this.checkedBox2 = []
        this.isIndeterminate1 = false,
        this.isIndeterminate2 = false,
        this.checkAll1 = false
        this.checkAll2 = false;
        this.$router.push({path: "admin_group"})
     }
    }
  };
</script>
<style scoped="scoped" lang="less">
	.add_group{
		margin-left: 30px;
	}
	.content_gruops{
		margin: 20px 0;
	}
	.content_list{
		display: flex;
		justify-content: flex-start;
		align-items: center;
		margin: 20px 0 20px 80px;
		.content_list_span{
			margin: 0 20px 0 40px;
			font-size: 14px;
			width: 90px;;
		}
	}
	.content_permissions,{
		margin: 10px 0;
	}
	.user_permissions{
		margin: 40px 0 120px 0;
	}
</style>


