import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login'
import MainPages from '@/components/main_pages'
//内容列表块
import Content from '@/components/pages/content_manage/content'
import ContentList from '@/components/pages/content_manage/content_list'
import AddPost from '@/components/pages/content_manage/add_post'
//内容分类块
import ContentClassify from '@/components/pages/content_manage/content_classify'
import AddClassify from '@/components/pages/content_manage/add_classify'
//帖子评论
import PostComment from '@/components/pages/content_manage/post_comment'

//用户列表
import User from '@/components/pages/user_manage/user'
import UserList from '@/components/pages/user_manage/user_list'
import UserAdd from '@/components/pages/user_manage/user_add'
import UserDetail from '@/components/pages/user_manage/user_detail'
//党支部分组
import PartyBranch from '@/components/pages/user_manage/party_branch'
import PartyBranchDetail from '@/components/pages/user_manage/partyBranch_detail'
import PartyBranchPeoples from '@/components/pages/user_manage/partyBranch_peoples'
import Participan from '@/components/pages/user_manage/participan'
import PartyBranchAdd from '@/components/pages/user_manage/partyBranch_add'
import Blacklist from '@/components/pages/user_manage/blacklist'
//排行榜
import PersonalRanking from '@/components/pages/leaderboard/personal_ranking'
import parytBranchRanking from '@/components/pages/leaderboard/parytBranch_ranking'
//轮播图
import CarouselList from '@/components/pages/carousel_manage/carousel_list'
import AddCarousel from '@/components/pages/carousel_manage/add_carousel'
//后台账号
import BackstageAccount from '@/components/pages/background_account/admin_list'
import AddAdmin from '@/components/pages/background_account/add_admin'
import AdminGroup from '@/components/pages/background_account/admin_group'
import AddGroup from '@/components/pages/background_account/add_group'
//活动
import QuestionAnswet from '@/components/pages/activity/qust_ansr'
import AddQuestionAnswet from '@/components/pages/activity/add_qust_ansr'
import QustAnsrPreview from '@/components/pages/activity/qust_ansr_preview'
import SignIn from '@/components/pages/activity/sign_in'
import SignInCheck from '@/components/pages/activity/sign_in_check'
import SignInAdd from '@/components/pages/activity/sign_in_add'
//设置
import SensitiveWords from '@/components/pages/setting/sensitive_words'
import AddWords from '@/components/pages/setting/add_words'
Vue.use(Router)

export default new Router({
	routes: [
		{ path: '*', redirect: '/login' },																														//登录页面
		{ path: '/login', name: 'Login', component: Login },
		{ path: '/main_pages', name: '智慧党建', component: MainPages, children:[
			{ path: '/content', name: '内容', component: Content, children: [
				{ path: '/content_list', name: '内容列表', component: ContentList},					//内容列表
				{ path: '/add_post', name: '新建帖子', component: AddPost},							//新建帖子
				{ path: '/content_classify', name: '内容分类', component: ContentClassify},			//内容分类
				{ path: '/add_classify', name: '新建帖子分类', component: AddClassify},				//新建帖子分类
				{ path: '/post_comment', name: '帖子评论', component: PostComment},					//帖子评论
			]},	
			{ path: '/user', name: '用户', component: User, children: [
				{ path: '/user_list', name: '用户列表', component: UserList},							//用户列表
				{ path: '/user_add', name: '添加用户', component: UserAdd},							//添加用户
				{ path: '/user_detail', name: '用户详情', component: UserDetail},						//用户详情
				{ path: '/party_branch', name: '党支部分组', component: PartyBranch},					//党支部分组
				{ path: '/partyBranch_detail', name: '党支部详情', component: PartyBranchDetail},		//党支部详情
				{ path: '/partyBranch_peoples', name: '所有人', component: PartyBranchPeoples},		//所有人
				{ path: '/participan', name: '查看参与人', component: Participan},						//查看参与人
				{ path: '/partyBranch_add', name: '添加党支部', component: PartyBranchAdd},			//添加党支部
				{ path: '/blacklist', name: '黑名单', component: Blacklist},							//黑名单
			]},
			{ path: '/conten', name: '排行榜', component: Content, children: [
				{ path: '/personal_ranking', name: '个人排行榜', component: PersonalRanking},			//个人排行榜
				{ path: '/parytBranch_ranking', name: '党支部排行榜', component: parytBranchRanking},	//党支部排行榜
			]},
			{ path: '/conten', name: '轮播图管理', component: Content, children: [
				{ path: '/carousel_list', name: '轮播图列表', component: CarouselList},				//轮播图列表
				{ path: '/add_carousel', name: '新建轮播图', component: AddCarousel},					//党支部排行榜
			]},
			{ path: '/conten', name: '活动', component: Content, children: [
				{ path: '/qust_ansr', name: '问答活动', component: QuestionAnswet},					//问答活动
				{ path: '/add_qust_ansr', name: '新建问答', component: AddQuestionAnswet},			//新建问答
				{ path: '/qust_ansr_preview', name: '问答预览', component: QustAnsrPreview},			//问答预览
				{ path: '/sign_in', name: '每月签到', component: SignIn},								//每月签到
				{ path: '/sign_in_check', name: '签到查看', component: SignInCheck},					//签到查看
				{ path: '/sign_in_add', name: '新建签到', component: SignInAdd},						//新增签到
			]},
			{ path: '/conten', name: '设置', component: Content, children: [
				{ path: '/sensitive_words', name: '敏感词库', component: SensitiveWords},				//敏感词汇
				{ path: '/add_words', name: '添加敏感词汇', component: AddWords},				        //添加敏感词汇
			]},
			{ path: '/conten', name: '后台账号', component: Content, children: [
				{ path: '/admin_list', name: '管理员列表', component: BackstageAccount},			    //管理员列表
				{ path: '/add_admin', name: '添加管理员', component: AddAdmin},				        //添加管理员
				{ path: '/admin_group', name: '管理员分组', component: AdminGroup},				    //管理员分组
				{ path: '/add_group', name: '添加分组', component: AddGroup},				        //添加分组
			]},
		]},
	]
})


