<template>
	<div class="partyBranch_peoples">
		<!--用户列表-->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
				<!--<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>-->
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="操作" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
						<el-button type="text" size="small" @click="detail(scope.$index)">详情</el-button>
						<el-button type="text" size="small" @click="audit(scope.$index)">审核</el-button>
						<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "partyBranch_peoples",
		data(){
			return{
				tableHeade: [
					{name: "用户ID",    prop: "id",      width: 80, tooltip: false},
					{name: "用户手机号", prop: "phone",    width: 140, tooltip: false},
					{name: "昵称",      prop: "nickName", width: 160, tooltip: false},
					{name: "姓名",      prop: "name",     width: 100, tooltip: false},
					{name: "性别",      prop: "sex",      width: 100, tooltip: false},
					{name: "认证状态",   prop: "approve",  width: 120, tooltip: false},
					{name: "所属党支部", prop: "partyBranch",  width: "", tooltip: true},
				],
				tableData: [
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
				],
			}
		},
		methods:{
			//编辑
			edit(index,scope){
				console.log("编辑",index, scope)
			},
			//详情
			detail(index){
				this.$router.push({path: "user_detail"})
			},
			//审核
			audit(index){
				var that = this;
				console.log("审核",index)
				that.$confirm('审核是否通过？', '提示', {
		          	confirmButtonText: '通过',
		          	cancelButtonText: '拒绝',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '审核成功!'
		          	});
		        }).catch(() => {
		        	that.$prompt('请输入拒绝理由', '提示',{
		        		inputPattern:  /\S/,
		        		inputErrorMessage: "请输入拒绝理由",
		        		closeOnClickModal: false
		        	}).then(({ value }) => {
			          	that.$message({
			            	type: 'success',
			            	message: '拒绝原因提交成功'
			          	});
			        });         
		        });
			},
			//删除
			delet(index){
				var that = this;
				console.log("删除",index)
				that.$confirm('确定删除？', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {
		          	that.$message({
		            	type: 'info',
		            	message: '已取消删除'
		          	});          
		        });
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style>
</style>


