<template>
	<div class="sign_in_add">
		<el-form ref="form" :rules="rules" :model="form" label-width="120px">
			<el-form-item label="签到开启">
				<el-switch v-model="form.sign_in"></el-switch>
			</el-form-item>
			<el-form-item label="选择开始时间" prop="date1">
				<el-col :span="8">
					<el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
				</el-col>
			</el-form-item>
			<el-form-item label="选择结束时间" prop="date2">
				<el-col :span="8">
					<el-date-picker type="date" placeholder="选择日期" v-model="form.date2" style="width: 100%;"></el-date-picker>
				</el-col>
			</el-form-item>
			<el-form-item label="签到文案" prop="textarea">
				<el-col :span="8">
					<el-input type="textarea" :rows="4" resize="none" placeholder="请输入签到文案" v-model="form.textarea"></el-input>
				</el-col>
			</el-form-item>
			<el-form-item>
				<el-button size="medium" type="primary" @click="onSubmit('form')">确认</el-button>
				<el-button size="medium" @click="$router.go(-1)">取消</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "sign_in_add",
		data() {
			return {
				form: {
					sign_in: false,
					date1: "",
					date2: "",
					textarea: "",
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
          			date2: [{ type: 'date', required: true, message: '请选择时间', trigger: 'change' }],
          			textarea:[{ required: true, message: '请输入签到文案', trigger: 'blur' }]
          		}
			}
		},
		methods: {
			onSubmit(formName) {
				this.$refs[formName].validate((valid) => {
			        if (valid) {
			            console.log(this.form)
			        } else {
			            console.log('error submit!!');
			            return false;
			        }
		        });
			}
		}
	})
</script>

<style>

</style>


