<template>
	<div class="blacklist">
		<!--顶部功能栏-->
		<div class="top_fun">
			<el-form :inline="true" :rules="rules" :model="formInline" class="demo-form-inline">
				<el-row>
					<!--根据党支部查询-->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.partyBranch" placeholder="根据党支部查询">
								<el-option label="默认分类" value="2"></el-option>
								<el-option label="分类一" value="1"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!--根据审批查询-->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.approval" placeholder="根据审批查询">
								<el-option label="精华" value="0"></el-option>
								<el-option label="制定" value="2"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!--用户手机号或ID-->
					<el-col :span="3">
						<el-form-item>
							<el-input size="medium" v-model="formInline.userId" placeholder="用户手机号或ID"></el-input>
						</el-form-item>
					</el-col>
					<!--用户昵称-->
					<el-col :span="3">
						<el-form-item>
							<el-input size="medium" v-model="formInline.userName" placeholder="用户昵称"></el-input>
						</el-form-item>
					</el-col>
					<!--搜索-->
					<el-col :span="2">
						<el-form-item>
							<el-button size="medium" type="info" @click="search">搜索</el-button>
						</el-form-item>
					</el-col>
					<!--批量删除-->
					<el-col :span="2" :offset="6">
						<el-form-item>
							<el-button type="warning" size="small" @click="batchRemove">批量删除</el-button>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</div>
		<!--用户列表-->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="操作" width="220" fixed="right">
					<template slot-scope="scope">
						<!--<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>-->
						<el-button type="text" size="small" @click="detail(scope.$index)">详情</el-button>
						<el-button type="text" size="small" @click="remove(scope.$index)">移除黑名单</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name:"blacklist",
		data(){
			return{
				formInline: {
					partyBranch : '',			//党支部
					approval    : "",			//审批
					userId      : "",			//用户手机或id
					userName    : "",			//用户名称
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
					date2: [{ type: 'date', required: true, message: '请选择时间', trigger: 'change' }],
				},
				tableHeade: [
					{name: "用户ID",    prop: "id",      width: 80, tooltip: false},
					{name: "用户手机号", prop: "phone",    width: 140, tooltip: false},
					{name: "昵称",      prop: "nickName", width: 160, tooltip: false},
					{name: "姓名",      prop: "name",     width: 100, tooltip: false},
					{name: "性别",      prop: "sex",      width: 100, tooltip: false},
					{name: "认证状态",   prop: "approve",  width: 120, tooltip: false},
					{name: "所属党支部", prop: "partyBranch",  width: "", tooltip: true},
				],
				tableData: [
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
					{ id: 77, phone: '18937107705', nickName: '蓝色星辰', name: 'tony', sex:"男", approve: "成功", partyBranch: "中共党支部" },
				],
			}
		},
		methods: {
			//搜索
			search() {
				console.log('search!');
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				}).catch(() => {});
			},
			//编辑
			edit(index,scope){
				console.log("编辑",index, scope)
			},
			//详情
			detail(index){
				this.$router.push({path: "user_detail"})
			},
			//删除
			remove(index){
				var that = this;
				console.log("移除",index)
				that.$confirm('确定移除黑名单？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '移除黑名单成功!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.blacklist{
	 	.top_fun{
	 		margin-bottom: 20px;
	 	}
	 }
</style>


