<template>
	<div class="party_branch">
		<el-form :inline="true" :rules="rules" :model="formInline" class="demo-form-inline">
			<el-row>
				<!--党支部名称-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.partyBranch_name" placeholder="党支部名称"></el-input>
					</el-form-item>
				</el-col>
				<!--管理员名称-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.admin_name" placeholder="管理员名称"></el-input>
					</el-form-item>
				</el-col>
				<!--选择开始、结束时间-->
				<el-col :span="4">
					<el-form-item prop="date1">
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line">至</el-col>
				<el-col :span="4">
					<el-form-item prop="date2">
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--搜索-->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search">搜索</el-button>
					</el-form-item>
				</el-col>
				<!--新建党支部-->
				<el-col :span="2" :offset="2">
					<el-form-item>
						<el-button type="primary" size="small" @click="addGroup">添加分组</el-button>
					</el-form-item>
				</el-col>
				<!--批量删除-->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="small" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!--党支部分组列表-->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="操作" width="260" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)">编辑</el-button>
						<el-button type="text" size="small" @click="detail(scope.$index)">详情</el-button>
						<el-button type="text" size="small" @click="addBlacklist(scope.$index)">加入黑名单</el-button>
						<el-button type="text" size="small" @click="seeAll(scope.$index)">查看所有人</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "party_branch",
		data(){
			return{
				formInline: {
					partyBranch_name: "",			//党支部名称
					date1           : "",			//开始时间
					date2           : "",			//结束时间
					admin_name      : "",			//管理员名称
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
					date2: [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
				},
				tableHeade: [
					{name: "党支部ID",	prop: "id",  	width: 80, tooltip: false},
					{name: "党支部名称", 	prop: "name", 	width: "", tooltip: true},
					{name: "创建时间",	prop: "time", 	width: 180, tooltip: false},
					{name: "党支部人数",	prop: "pNum",	width: 100, tooltip: false},
					{name: "管理员",  	prop: "admin", 	width: 180, tooltip: false},
				],
				tableData: [
				{ id: 77, name: '蓝色星辰', time: '2017-12-07', pNum:"25", admin: "成功"},
				{ id: 77, name: '蓝色星辰', time: '2017-12-07', pNum:"25", admin: "成功"},
				{ id: 77, name: '蓝色星辰', time: '2017-12-07', pNum:"25", admin: "成功"},
				{ id: 77, name: '蓝色星辰', time: '2017-12-07', pNum:"25", admin: "成功"},
				{ id: 77, name: '蓝色星辰', time: '2017-12-07', pNum:"25", admin: "成功"},
				],
			}
		},
		methods:{
			//搜索
			search() {
				console.log('search!');
			},
			//新建分组
			addGroup(){
				this.$router.push({path: "/partyBranch_add"})
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				})
			},
			//编辑
			edit(index,scope){
				this.$router.push({path: "/partyBranch_add", query:{reName: "编辑分组"}})
			},
			//详情
			detail(index){
				this.$router.push({path: "partyBranch_detail"})
			},
			//加入黑名单
			addBlacklist(index){
				var that = this;
				that.$confirm('确定加入黑名单？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '添加黑名单成功!'
		          	});
		        }).catch(()=>{});
			},
			//查看所有人
			seeAll(index){
				this.$router.push({path: "partyBranch_peoples"})
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped lang="less">
	.party_branch {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


