<template>
	<div class="partyBranch_detail">
		<!--信息显示-->
		<el-form class="demo-form-inline" label-width="140px">
			<el-row>
				<el-col v-for="(item,index) in formData" :key="item.label" :span="item.span">
					<el-form-item :label="item.label">
						<p>{{item.value}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
							<el-button v-if="item.type && item.type=='btn'" type="text" size="small" @click="lookAll">查看所有人</el-button>
						</p>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--列表-->
		<table cellspacing="0" cellpadding="0">
			<tr v-for="item in tableData">
				<td>{{item.news}}</td>
				<td>参与度{{item.code}}</td>
				<td><el-button type="text" @click="lookParticipant">查看参与人</el-button></td>
			</tr>
		</table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "partyBranch_detail",
		data(){
			return{
				formData:[
					{label: "党支部ID：",		span: 24, value: "007"},
					{label: "党支部名称：", 	span: 24, value: "党支部1"},
					{label: "党支部人数：", 	span: 24, value: "36", type:"btn"},
					{label: "党支部管理员：", 	span: 24, value: "党支部1"},
					{label: "总参与度：", 		span: 24, value: "85%"},
					{label: "参与的帖子：", 	span: 24, value: "66个"},
				],
				tableData:[
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", code: "80%"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", code: "80%"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", code: "80%"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", code: "80%"},
				]
			}
		},
		methods:{
			//查看所有
			lookAll(){
				console.log("查看所有")
				this.$router.push({path: "partyBranch_peoples"})
			},
			//查看参与人
			lookParticipant(){
				this.$router.push({path: "participan"})
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style>
	.partyBranch_detail .el-row .el-col .el-form-item { margin-bottom: 5px; }
	.partyBranch_detail .el-pagination{margin-right: 20%;}
</style>
<style scoped="scoped" lang="less">
	.partyBranch_detail{
		table{
			margin-top: 10px;
			width: 80%;
			font-size: .95rem;
			border: 1px solid #E6E6E6;
			tr{
				td{
					padding: 8px;
					height: 25px;
					line-height: 25px;
					text-align: center;
					border: 1px solid #E6E6E6; 
					&:nth-child(1){
						text-align: left;
						color: #235DD4;
						text-decoration: underline;
						&:hover{cursor: pointer;}
					}
				}
			}
		}
	}
</style>


