<template>
	<div class="add_post">
		<el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="120px">
			<el-row>
				<el-col >
					<el-form-item label="标题：" prop="title">
						<el-input v-model="form.title" placeholder="请输入标题"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>	
				<el-col>
					<el-form-item label="选择分类：" prop="classify">
						<el-select v-model="form.classify" placeholder="请选择分类">
							<el-option label="区域一" value="shanghai"></el-option>
							<el-option label="区域二" value="beijing"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<!--富文本-->
			<el-row>
				<UE_editor :defaultMsg='defaultMsg' :config="config" ref="ue"></UE_editor>
			</el-row>
			<el-form-item class="btns">
			    <el-button size="medium" type="primary" @click="submit('form')">发布</el-button>
			    <el-button size="medium" @click="back('form')">返回</el-button>
  			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import UE_editor from '@/components/template/editor.vue';
	export default({
		name: "add_post",
		data() {
			return {
				form: {
					title: "",
					classify: "",
					editor_content: "",
				},
				defaultMsg: '',
		        config: {
		          	initialFrameWidth  : 800,		//宽度
		          	initialFrameHeight : 350,		//高度
		          	indentValue		   : '2em',		//首行缩进
		          	wordCount          : false,		//字数统计
		          	elementPathEnabled :false,		//是否启用路径
		          	zIndex             : 9,			//层级
		        },
		        rules: {
		          	title: [
		            	{ required: true, message: '请输入标题', trigger: 'blur' }
		          	],
		          	classify: [
		            	{ required: true, message: '请选择分类', trigger: 'change' }
		          	]
		        }
			}
		},
		components: {UE_editor},
		created() {

		},
		methods: {
			//发布
			submit(form){
				var that = this;
				//获取富文本内容
				that.form.editor_content = this.$refs.ue.getUEContent();
				//验证
				that.$refs[form].validate((valid) => {
	          		if (valid) {
	            		console.log(this.form)
	          		} else {
	            		console.log('提交数据验证错误!!');
	            		return false;
	          		}
        		});
			},
			//返回
			back(){
				this.$router.go(-1)
			}
		}
	})
</script>

<style type="text/css">
	.add_post #editor{ margin-left: 120px; }
</style>
<style scoped="scoped" lang="less">
	.btns{
		margin-left: 120px;
		margin-top: 30px;
	}
</style>


