<template>
	<div class="qust_ansr_preview">
		<el-button class="back" type="primary" icon="el-icon-arrow-left" @click="$router.go(-1)">返回编辑页</el-button>
		<br />
		<br />
		<div class="preview">
			<h2>问卷标题</h2>
			<div class="remark">
				为了给您提供更好的服务，希望您能抽出几分钟时间，将您的感受和建议告诉我们，我们非常重视每位用户的宝贵意见，期待您的参与！现在我们就马上开始吧！
			</div>
			<div class="question_list">
				<div v-for="(item,index) in 6" class="question_list_item">
					<p class="qust_title">{{index+1}}、你猜猜</p>
					<div v-if="index%2==0" class="answers_select">
		  				<p><input type="radio" name="radio" />5%</p>
		  				<p><input type="radio" name="radio" />15%</p>
		  				<p><input type="radio" name="radio" />25%</p>
		  			</div>
		  			<div v-else="" class="answers_select">
		  				<p><input type="checkbox" name="" />5%</p>
		  				<p><input type="checkbox" name="" />15%</p>
		  				<p><input type="checkbox" name="" />25%</p>
		  			</div>
				</div>
			</div>
			<br />
			<div class="submit_div">
				<el-button type="primary" size="medium">提交</el-button>
			</div>
		</div>
	</div>
</template>

<script>
	export default({
		name: "qust_ansr_preview",
		data(){
			return{
				
			}
		},
		methods:{
			
		}
	})
</script>

<style scoped="scoped" lang="less">
	.qust_ansr_preview{
		button.back{
			margin-left: 60px;
		}
		.preview{
			padding: 20px;
			margin-left: 60px;
			width: 360px;
			height: 650px;
			overflow-y: auto;
			border: 2px solid #CCCCCC;
			border-radius: 5px;
			box-shadow: 0 0 15px #ccc;
			h2{
				margin: 20px 0;
				text-align: center;
			}
			.remark{
				text-indent: 2rem;
				text-align: justify;
				margin-bottom: 20px;
			}
			.question_list{
				.question_list_item{
					margin-bottom: 20px;
					.qust_title{
						padding: 5px;
						margin-bottom: 10px;
						background: #f0f0f0;
					}
					.answers_select{
						p{
							margin: 12px 0;
							input{margin-right: 10px; margin-left: 25px;}
						}
					}
				}
			}
			.submit_div{
				text-align: center;
			}
		}
	}
</style>


