<template>
	<div class="login">
		<!--顶部显示-->
		<header class="flex_around_v">
			<div class="head_left flex_between_v">
				<img src="../../static/images/logo.png" width="40" height="40"/>
				<span>智慧党建 后台管理平台</span>
			</div>
			<div class="head_right flex_start_v">
				<img src="../../static/images/dl.png" width="16"/>
				<span @click="login_flag=true">登录</span>
			</div>
		</header>
		<div class="main" :style="{height: document_height-60+'px'}">
			<!--登录部分-->
			<div v-if="login_flag" class="main_div login_div">
				<h4>密码登录</h4>
				<input type="text" v-model="account" placeholder="帐号"/>
				<input type="password" v-model="password" placeholder="密码"/>
				<div class="flex_between_v">
					<input type="text" v-model="verification" placeholder="验证码"/>
					<img src="../../static/images/yzm.png" height="32" title="点击重新获取验证码" @click="changeVerImg"/>
				</div>
				<button class="bg_gradient" @click="login">登录</button>
				<div class="funs flex_between_v">
					<label for="remLogin" class="flex_start_v">
						<input id="remLogin" type="checkbox" v-model="rem_login" />三天内自动登录
					</label>
					<span @click="forgetPWD">忘记密码</span>
				</div>
			</div>
			<!--找回密码-->
			<div v-else class="main_div find_pwd">
				<h4>找回密码</h4>
				<input type="text" v-model="find_account" placeholder="帐号/手机号"/>
				<div class="flex_between_v">
					<input type="text" v-model="phone_ver" placeholder="短信验证码"/>
					<span class="getPhoneCode bg_gradient" @click="get_phone_ver">获取短信验证码</span>
				</div>
				<input type="password" v-model="new_password" placeholder="请输入您的新密码"/>
				<button class="bg_gradient" @click="modify">确认修改</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name: "login",
		data(){
			return{
				document_height: document.documentElement.clientHeight,		//屏幕高度
				account     : "",		//账户
				password    : "",		//密码
				verification: "",		//验证码
				rem_login   : false,	//自动登录
				login_flag  : true,		//登录 忘记密码判断
				find_account: "",		//找回帐号
				phone_ver   : "",		//短信验证码
				new_password: "",		//新密码
			}
		},
		created(){
			console.log(this.document_height)
		},
		methods:{
			//登录
			login(){
				console.log('登录')
				this.$router.push({path: "/content_list"})
			},
			//重获验证码
			changeVerImg(){
				console.log('点击重新获取验证码')
			},
			//忘记密码
			forgetPWD(){
				this.login_flag = !this.login_flag
			},
			//获取短信验证码
			get_phone_ver(){
				console.log('获取短信验证码')
			},
			//确认修改
			modify(){
				console.log('确认修改')
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.login{
		header{
			padding: 10px 0;
			img{margin-right: 10px;}
			.head_left{
				span{font-size: 1.2rem; font-weight: bold;}
			}
			.head_right{
				&:hover{
					cursor: pointer;
					color: #00deff;
				}
			}
		}
		.main{
			position: relative;
			width: 100%;
			height: 800px;
			background: url("../../static/images/zt10.png") 0 0 no-repeat;
			background-size: 100% auto;
			.main_div{
				position: absolute;
				top: 25%;
				left: 50%;
				padding: 20px;
				width: 260px;
				height: 280px;
				background: white;
				box-shadow: 0 0 5px #ccc;
				h4{margin-bottom: 20px;}
				input[type='text'],input[type='password']{
					padding: 0 5px;
					margin: 8px 0;
					height: 30px;
					width: calc(~"100% - 10px");
					border: 1px solid #CCCCCC;
					border-radius: 3px;
				}
				button{
					padding: 5px 0;
					margin: 20px 0;
					height: 35px;
					width: 100%;
					color: white;
					border: none;
					border-radius: 3px;
					&:hover{ cursor: pointer; }
				}
				.funs{
					font-size: .85rem;
					color: #888;
					label{
						input[type='checkbox']{
							margin-right: 5px;
						}
						&:hover{
							cursor: pointer;
						}
					}
					span:hover{cursor: pointer;}
				}
			}
			.find_pwd{ 
				height: 260px; 
				.getPhoneCode{
					display: inline-block;
					padding: 0 5px;
					margin-left: 5px;
					height: 32px;
					width: 170px;
					text-align: center;
					line-height: 32px;
					font-size: .85rem;
					color: white;
					border-radius: 3px;
					&:hover{cursor: pointer;}
				}
			}
		}
	}
</style>


