<template>
	<div class="add_admin">
		<el-form label-position="right" label-width="120px" :model="formInline" :rules="rules" ref="formInline">
			<el-row>
				<el-col :span="8">
					<el-form-item label="设置账号" prop="account_number">
				    	<el-input v-model="formInline.account_number" placeholder="6到12位数字或英文字母"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="设置密码" prop="set_password">
				    	<el-input type="password" v-model="formInline.set_password" placeholder="6到12位数字或英文字母"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="确认密码" prop="agree_password">
				    	<el-input type="password" v-model="formInline.agree_password" placeholder="再次输入密码"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="管理员姓名" prop="admin_name">
				    	<el-input v-model="formInline.admin_name" placeholder="请输入管理员姓名"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="管理员分组" prop="region">
					    <el-select v-model="formInline.region" placeholder="总管理" size="large">
					      <el-option label="总管理" value="0"></el-option>
					      <el-option label="党支部1" value="1"></el-option>
					      <el-option label="党支部2" value="2"></el-option>
					    </el-select>
					</el-form-item>
				</el-col>
				<el-col :span="2" :offset="1">
					<el-button size="medium" type="text" @click="addGroup">新建分组</el-button>
				</el-col>
			</el-row>
			<el-row>
				<el-col>
					<el-form-item>
					    <el-button type="primary" size="medium" @click="submitForm('formInline')">提交</el-button>
					    <el-button size="medium" @click="resetForm('formInline')">重置</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name : "add_admin" ,
		data(){
			return {
				formInline:{
					account_number:"",
					set_password : "" ,
					agree_password : "" ,
					admin_name : "" ,
					region : "" ,
				},
				rules:{
					account_number: [
			            { required: true, message: '请输入账号', trigger: 'blur' },
			            { min: 6, max: 12, message: '长度在 6到 12 个字符', trigger: 'blur' }
			        ],
			        set_password: [
			            { required: true, message: '请设置密码', trigger: 'blur' },
			            { min: 6, max: 12, message: '长度在 6到 12 个字符', trigger: 'blur' }
			        ],
			        agree_password: [
			            { required: true, message: '请确认密码', trigger: 'blur' },
			            { min: 6, max: 12, message: '长度在 6到 12 个字符', trigger: 'blur' }
			        ],
			        admin_name: [
			            { required: true, message: '请输入管理员姓名', trigger: 'blur' },
			        ],
			        region: [
			            { required: true, message: '请选择管理员分组', trigger: 'change' }
			        ],
				}
			}
		},
		methods:{
			addGroup(){
				this.$router.push({path: "add_group"})
			},
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		            alert('submit!');
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		    },
		    resetForm(formName) {
		        this.$refs[formName].resetFields();
		    }
		}
	})
</script>

<style type="text/css">
	.add_admin .el-form-item .el-select.el-select--large{width: 100%;}
</style>
<style scoped="scoped" lang="less">
	.add_admin{
		margin-left: 60px;
	}
</style>


