<template>
	<div class="sensitive_words">
		<el-form :inline="true"  class="demo-form-inline">
			<el-row>
				<el-col :span="2" :offset="15">
					<el-form-item>
						<el-button type="primary" size="medium" @click="addWords">新建敏感词</el-button>
					</el-form-item>
				</el-col>
				<el-col :span="2" :offset="1">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--管理员分组列表-->
		<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width:90%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
		    <el-table-column v-for="item in tableHeader" :key="item.prop" :prop="item.prop" :label="item.name" :show-overflow-tooltip="item.tooltip" :width="item.width" align="center"></el-table-column>
		    <el-table-column align="center" label="操作" width="260" fixed="right">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click="delet(scope.$index)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "sensitive_words",
		data(){
			return{
				tableHeader:[
		      		{ name:"词语ID" , prop:"word_id"   , width:140 , tooltip:false},
		      		{ name:"词语"   , prop:"word_title", width:"" ,  tooltip:true},
		        ],
		        tableData: [
		      		{word_id: '100',word_title: 'fuck'},
		      		{word_id: '101',word_title: 'fuck'},
		      		{word_id: '102',word_title: 'fuck'},
		      		{word_id: '103',word_title: 'fuck'},
		      		{word_id: '104',word_title: 'fuck'},
		      		{word_id: '105',word_title: 'fuck'},
		      		{word_id: '106',word_title: 'fuck'},
			    ],
			}
		},
		methods:{
			//删除
			delet(index){
				var that = this;
				that.$confirm('确定删除吗？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(()=>{});
			},
			//新建敏感词汇
			addWords(){
				this.$router.push({path:"add_words"})
			},
			//批量删除
			batchRemove(){
				var that = this;
				that.$confirm('批量删除所选？', '提示', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
				})
			},
			//全选
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
		        console.log(val)
		    },
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	
</style>


