<template>
	<div class="user_detail">
		<!--信息显示-->
		<el-form class="demo-form-inline" label-width="100px">
			<el-row>
				<el-col v-for="(item,index) in formData" :key="item.label" :span="item.span" :pull="item.pull">
					<el-form-item :label="item.label">
						<img v-if="item.type && item.type=='img'" :src="item.value" width="180"/>
						<p v-else>{{item.value}}</p>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!--列表-->
		<table cellspacing="0" cellpadding="0">
			<tr v-for="item in tableData">
				<td @click="newsDetail(item.link)">{{item.news}}</td>
				<td>{{item.code}}</td>
				<td>{{item.fun}}</td>
			</tr>
		</table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "user_detail",
		data(){
			return{
				formData:[
					{label: "用户ID：",		span: 12, pull: 0, value: "007"},
					{label: "用户手机号：", 	span: 12, pull: 4, value: "18965863666"},
					{label: "用户姓名：",  	span: 12, pull: 0, value: "纳兰容若"},
					{label: "昵称：",     	span: 12, pull: 4, value: "蓉儿"},
					{label: "性别：",     	span: 12, pull: 0, value: "男"},
					{label: "所属党支部：", 	span: 12, pull: 4, value: "中共党支部"},
					{label: "身份证号：",  	span: 24, pull: 0, value: "411330199901568868"},
					{label: "用户头像：",  	span: 24, pull: 0, type: "img", value: "../../../static/images/picture_1.jpg"},
					{label: "用户总分：",  	span: 24, pull: 0, value: "99999分"},
					{label: "加分记录：",  	span: 24, pull: 0, value: "33096个"},
				],
				tableData:[
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "评论"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "点赞"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "评论"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "点赞"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "点赞"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "评论"},
					{news: "学思践悟·十九大打造共建共治共享的社会治理格局", link: "", code: "+5分", fun: "点赞"},
				]
			}
		},
		methods:{
			//新闻详情
			newsDetail(link){
				this.$router.push({path:link})
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style>
	.user_detail .el-row .el-col .el-form-item { margin-bottom: 5px; }
	.user_detail .el-row .el-col .el-form-item img { margin-top: 13px; }
	.user_detail .el-pagination{margin-right: 20%;}
</style>
<style scoped="scoped" lang="less">
	.user_detail{
		table{
			margin-top: 10px;
			width: 80%;
			font-size: .95rem;
			border: 1px solid #E6E6E6;
			tr{
				td{
					padding: 8px;
					height: 25px;
					line-height: 25px;
					text-align: center;
					border: 1px solid #E6E6E6; 
					&:nth-child(1){
						text-align: left;
						color: #235DD4;
						text-decoration: underline;
						&:hover{cursor: pointer;}
					}
				}
			}
		}
	}
</style>


