<template>
	<div class="post_comment">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.postName" placeholder="帖子名称"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.userName" placeholder="用户名"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="4">
					<el-form-item>
						<el-input size="medium" v-model="formInline.comment" placeholder="输入评论内容"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-button size="medium" type="primary" @click="search">搜索</el-button>
					</el-form-item>
				</el-col>
				<el-col :span="3" :offset="3">
					<el-form-item>
						<el-button size="medium" type="primary" plain @click="all">全部（1229）</el-button>
					</el-form-item>
				</el-col>
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="warning" plain @click="batch_delet">批量删除</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!--列表-->
		<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 95%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55"></el-table-column>
			<el-table-column align="center" prop="comment_user" label="评论用户" width="140"></el-table-column>
			<el-table-column align="center" prop="post_name" label="归属帖子名称" width="200"></el-table-column>
			<el-table-column align="center" prop="post_time" label="评论时间" width="160"></el-table-column>
			<el-table-column align="center" prop="post_content" label="评论内容" show-overflow-tooltip></el-table-column>
			<el-table-column align="center" fixed="right" label="操作" width="100">
		      	<template slot-scope="scope">
			        <el-button type="text" size="small" @click="delet(scope.row)">删除</el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "post_comment",
		data() {
			return {
				formInline: {
		          	postName: '',
		          	userName: '',
		          	comment : '',
		        },
		        tableData: [
					{comment_user: "陶冶", post_name:"当前用来存储全部的帖子", post_time:"2017-12-07", post_content: 999},
					{comment_user: "廖立伟", post_name:"当前用来存储全部的帖子", post_time:"2017-12-07", post_content: 999},
					{comment_user: "李鹏林", post_name:"当前用来存储全部的帖子", post_time:"2017-12-07", post_content: 999},
					{comment_user: "俞江南", post_name:"当前用来存储全部的帖子", post_time:"2017-12-07", post_content: 999},
				]
			}
		},
		methods: {
			//搜索
			search() {
				console.log('submit!');
			},
			//全部
			all(){
				
			},
			//批量删除
			batch_delet(){
				
			},
			//批量选择
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//删除
			delet(index){
				var that = this;
				console.log("删除",index)
				that.$confirm('确定删除？', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`当前页: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`当前页: ${val}`);
      		}
		}
	})
</script>

<style>

</style>


