<template>
	<div class="MainPages">
		<div class="header flex_start_v">
			<img class="head_logo" src="../../static/images/z-8.png" height="60" />
			<div class="head_info flex_between_v">
				<span>后台管理平台</span>
				<el-dropdown trigger="click">
					<span class="el-dropdown-link">
				        2585469768@qq.com<i class="el-icon-arrow-down el-icon--right"></i>
				    </span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>退出</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>
		<!--主体-->
		<div class="content flex_start">
			<!--侧栏-->
			<div class="aside" :style="{height: contentHeight+ 'px'}">
				<el-row>
					<el-col>
						<el-menu :default-active="nav_active" :unique-opened="true" :router="true" 
							class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" 
							background-color="#2b2e32" text-color="#fff" active-text-color="#00deff">
							<div v-for="(item,index) in navList" :key="item.name">
								<el-submenu v-if="item.children" :index="item.name">
									<template slot="title">
										<img :src="item.icon" width="13" />
										<span>{{item.name}}</span>
									</template>
									<el-menu-item v-for="it in item.children" :key="it.name" :index="it.link">{{it.name}}</el-menu-item>
								</el-submenu>
								<el-menu-item v-else :index="item.link">
									<img :src="item.icon" width="13" />
									<span slot="title">{{item.name}}</span>
								</el-menu-item>
							</div>
						</el-menu>
					</el-col>
				</el-row>
			</div>
			<!--主体-->
			<div class="main" :style="{minHeight: contentHeight-60+ 'px'}">
				<!--面包屑-->
				<tem_breadcrumb :rename="router_reName"></tem_breadcrumb>
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	import shouye from '../../static/images/nr.png'
	import paihang from '../../static/images/phb.png'
	import lunbo from '../../static/images/lunbo.png'
	import htzh from '../../static/images/htzh.png'
	import huodong from '../../static/images/hd.png'
	import shezhi from '../../static/images/sz.png'
	import yonghu from '../../static/images/user.png'

	import tem_breadcrumb from '@/components/template/breadcrumb'

	export default {
		name: "MainPages",
		data() {
			return {
				contentHeight  : 0, 	//窗口高度
				router_reName  : "",	//路由名称重定义
				nav_active     : "",	//侧栏导航选中分类
				navList: [{
						name: '内容',
						icon: shouye,
						children: [{
							name: "内容列表",
							link: 'content_list' 
						}, {
							name: "内容分类",
							link: 'content_classify'
						}, {
							name: "帖子评论",
							link: 'post_comment'
						}]
					},
					{
						name: '用户',
						icon: yonghu,
						children: [{
							name: "用户列表",
							link: 'user_list'
						}, {
							name: "党支部分组",
							link: 'party_branch'
						}, {
							name: "黑名单",
							link: 'blacklist'
						}]
					},
					{
						name: '排行榜',
						icon: paihang,
						children: [{
							name: "个人排行榜",
							link: 'personal_ranking'
						}, {
							name: "党支部排行榜",
							link: 'parytBranch_ranking'
						}]
					},
					{
						name: '轮播图管理',
						icon: lunbo,
						children: [{
							name: "轮播图列表",
							link: 'carousel_list'
						}]
					},
					{
						name: '后台帐号',
						icon: htzh,
						children: [{
							name: "管理员列表",
							link: 'admin_list'
						}, {
							name: "管理员分组",
							link: 'admin_group'
						}]
					},
					{
						name: '活动',
						icon: huodong,
						children: [{
							name: "问答活动",
							link: 'qust_ansr'
						}, {
							name: "每月签到",
							link: 'sign_in'
						}]
					},
					{
						name: '设置',
						icon: shezhi,
						children: [{
							name: "敏感词库",
							link: 'sensitive_words'
						}]
					}
				]
			}
		},
		components: {
			tem_breadcrumb
		},
		created() {
			this.contentHeight = window.innerHeight - 60;
			this.nav_active = this.$route.matched[this.$route.matched.length-1].path.slice(1);
			console.log("$route.matched:",this.$route.matched)
		},
		methods: {
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			}
		},
		 watch: {
		    '$route' (to, from) {
		    	console.log(from.path)
		      	this.router_reName = to.query.reName ? to.query.reName : "";
		    }
		}
	}
</script>

<style type="text/css">
	.el-submenu .el-menu-item { min-width: 100px; }
	.el-submenu .el-submenu__title { background: #1f1f1f !important; }
	.el-dropdown-selfdefine{color: white; cursor: pointer;}
</style>
<style scoped="scoped" lang="less">
	.MainPages {
		.header {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			color: white;
			background: #2b2e32;
			z-index: 10;
			.head_info {
				padding: 0 20px;
				width: calc(~"100% - 207px");
			}
		}
		.aside {
			position: fixed;
			top: 60px;
			width: 167px;
			overflow-y: auto;
			background: #2b2e32;
			z-index: 10;
			&::-webkit-scrollbar {
				width: 0;
			}
		}
		.main {
			margin: 10px;
			margin-top: 70px;
			margin-left: 177px;
			padding: 20px;
			padding-top: 20px;
			width: calc(~"100% - 217px");
			background: white;
		}
	}
</style>


