<template>
	<el-breadcrumb separator-class="el-icon-arrow-right">
	  	<el-breadcrumb-item v-for="(item,index) in $route.matched" :key="item.path">{{(rename && index+1==$route.matched.length)?rename:item.name}}</el-breadcrumb-item>
	</el-breadcrumb>
</template>

<script>
	export default({
		name:"breadcrumb",
		props: ['rename'],
		data(){
			return{
				reName:"",
				router:""
			}
		},
		created(){
			this.reName = this.$route.query.reName
			console.log(this.reName)
			console.log(this.$route.matched.length)
			
		},
		methods:{
			
		}
	})
</script>

<style>
	.el-breadcrumb{margin-bottom: 40px;}
</style>


