<template>
	<div class="partyBranch_add">
		<el-form  :rules="rules" :model="formInline" ref="formInline" label-width="160px" class="demo-form-inline">
			<el-row>
				<el-col :span="8">
					<el-form-item label="党组织名称："prop="partyBranchName">
						<el-input size="medium" v-model="formInline.partyBranchName" placeholder="党组织名称"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="添加党组织管理员：" prop="adminName">
						<el-input size="medium" v-model="formInline.adminName" placeholder="党组织管理员名称"></el-input>
					</el-form-item>
				</el-col>
				<el-col v-if="addAdmin" :span="2" :pull="2">
					<el-form-item>
						<el-button type="text" size="medium" @click="addAdmin=!addAdmin">添加一名</el-button>
					</el-form-item>
				</el-col>	
			</el-row>
			<el-row v-if="!addAdmin">
				<el-col :span="8">
					<el-form-item prop="adminName_2">
						<el-input size="medium" v-model="formInline.adminName_2" placeholder="党组织管理员名称"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-form-item>
				<el-button type="primary" size="medium" @click="onSubmit('formInline')">提交</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "partyBranch_add",
		data() {
			return {
				addAdmin  : true,
				formInline: {
					partyBranchName: "",	//党组织名称
					adminName      : "",	//党组织管理员名称
					adminName_2    : "",	//党组织管理员名称
				},
				rules: {
					partyBranchName : [{ required: true, message: '请输入党组织名称', trigger: 'blur' }],
					adminName       : [{ required: true, message: '请输入党组织管理员名称', trigger: 'blur' }],
					adminName_2     : [{ required: true, message: '请输入党组织管理员名称', trigger: 'blur' }],
				}
			}
		},
		methods:{
			//提交
			onSubmit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("提交成功")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			}
		}
	})
</script>

<style>
	
</style>


