<template>
	<div class="personal_ranking">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<el-col :span="4">
					<el-form-item>
						<p style="height: 35px; overflow: hidden;">手机端数据显示时间段选择：</p>
					</el-form-item>
				</el-col>
				<!--选择显示开始、结束时间-->
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="showTime1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line">至</el-col>
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="showTime2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--开启-->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="small" @click="openTime">开启</el-button>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<!--根据党支部查询-->
				<el-col :span="3">
					<el-form-item>
						<el-select size="medium" v-model="formInline.partyBranch" placeholder="根据党支部查询">
							<el-option label="默认分类" value="2"></el-option>
							<el-option label="分类一" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<!--用户手机号或ID-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.userID" placeholder="用户手机号或ID"></el-input>
					</el-form-item>
				</el-col>
				<!--用户昵称-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.nickname" placeholder="用户昵称"></el-input>
					</el-form-item>
				</el-col>
				<!--用户积分-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.usercode" placeholder="输入用户积分"></el-input>
					</el-form-item>
				</el-col>
				<!--选择开始、结束时间-->
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择开始时间" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line">至</el-col>
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="选择终止时间" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!--搜索-->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search">搜索</el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!--表格-->
		<el-table :data="tableData" style="width: 100%" :default-sort = "{prop: 'No', order: 'descending'}" >
		    <el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip" :sortable="item.sortable"></el-table-column>
				<el-table-column align="center" label="操作" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="detail(scope.$index)">详情</el-button>
						<el-button type="text" size="small" @click="remove(scope.$index)">移除</el-button>
						<el-button type="text" size="small" @click="toBlacklist(scope.$index)">加入黑名单</el-button>
					</template>
				</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default({
		name:"personal_ranking",
		data(){
			return{
				formInline: {
					partyBranch     : "",			//党支部查询
					userID          : "",			//用户手机号或id
					nickname        : "",			//用户昵称
					usercode        : "",			//用户积分
					date1           : "",			//开始时间
					date2           : "",			//结束时间
				},
				showTime1 : "",						//展示起始时间
				showTime2 : "",						//展示终止时间
				tableHeade: [
					{name: "排名",    	prop: "No",      	width: 100,	tooltip: false, sortable: true},
					{name: "用户名", 		prop: "name",    	width: 140, tooltip: false},
					{name: "所属党总支部",	prop: "partyBranch",width: "", 	tooltip: true},
					{name: "总积分",   	prop: "totalCode",	width: 100, tooltip: false},
					{name: "最新排行时间",	prop: "rankTime",	width: 160, tooltip: false},
				],
				tableData: [
					{ No: '01', name: '王小虎', partyBranch: '上海市普陀区金沙江路 1518 弄', totalCode:"888", rankTime:"2017-12-08" },
					{ No: '02', name: '王小虎', partyBranch: '上海市普陀区金沙江路 1518 弄', totalCode:"888", rankTime:"2017-12-07" },
					{ No: '03', name: '王小虎', partyBranch: '上海市普陀区金沙江路 1518 弄', totalCode:"888", rankTime:"2017-12-06" },
					{ No: '04', name: '王小虎', partyBranch: '上海市普陀区金沙江路 1518 弄', totalCode:"888", rankTime:"2017-12-05" },
					{ No: '05', name: '王小虎', partyBranch: '上海市普陀区金沙江路 1518 弄', totalCode:"888", rankTime:"2017-12-04" },
				]
			}
		},
		methods:{
			//开启时间
			openTime(){
				
			},
			//搜索、
			search(){
				
			},
			//详情
			detail(index){
				this.$router.push({path: "user_detail"})
			},
			//移除
			remove(index){
				var that = this;
				console.log("删除",index)
				that.$confirm('确定移除？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '移除成功!'
		          	});
		        }).catch(() => {});
			},
			//加入黑名单
			toBlacklist(index){
				var that = this;
				console.log("黑名单",index)
				that.$confirm('确定加入黑名单？', '提示', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '加入黑名单成功!'
		          	});
		        }).catch(() => {});
			}
		}
	})
</script>

<style scoped lang="less">
	.personal_ranking {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


